@font-face {
  font-family: "InterTight";
  font-display: swap;
  src: url("../fonts/InterTight/InterTight-Regular.woff2") format("woff2"), url("../fonts/InterTight/InterTight-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: "InterTight";
  font-display: swap;
  src: url("../fonts/InterTight/InterTight-Medium.woff2") format("woff2"), url("../fonts/InterTight/InterTight-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
}

@font-face {
  font-family: "InterTight";
  font-display: swap;
  src: url("../fonts/InterTight/InterTight-Bold.woff2") format("woff2"), url("../fonts/InterTight/InterTight-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "InterTight";
  font-display: swap;
  src: url("../fonts/InterTight/InterTight-SemiBold.woff2") format("woff2"), url("../fonts/InterTight/InterTight-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: "Jeko";
  font-display: swap;
  src: url("../fonts/Jeko/Jeko-Bold.woff2") format("woff2"), url("../fonts/Jeko/Jeko-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
}

@font-face {
  font-family: "Jeko";
  font-display: swap;
  src: url("../fonts/Jeko/Jeko-ExtraBold.woff2") format("woff2"), url("../fonts/Jeko/Jeko-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
}

@font-face {
  font-family: "Jeko";
  font-display: swap;
  src: url("../fonts/Jeko/Jeko-Black.woff2") format("woff2"), url("../fonts/Jeko/Jeko-Black.woff") format("woff");
  font-weight: 900;
  font-style: normal;
}

:root {
  --font-family: "InterTight", sans-serif;
  --font-family-second: "Jeko", sans-serif;
  --color-white: #ffffff;
  --color-black: #191919;
  --color-modal-bg: rgba(0, 0, 0, 0.6);
  --vh: 100vh;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

input,
button,
textarea {
  background-color: transparent;
  font-family: inherit;
}

textarea {
  resize: none;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

input[type=number] {
  -moz-appearance: textfield;
}

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0px;
}

a,
button {
  color: inherit;
  font-size: inherit;
}

body {
  font-family: var(--font-family);
  font-weight: 400;
  color: var(--color-black);
  --padding-container: 10px;
  --container-width: 1120px;
  --max-width-container: calc(var(--container-width) + var(--padding-container) * 2);
  --indent-section: 40px;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.main {
  flex: 1 1 auto;
}

[class*=container] {
  padding-left: var(--padding-container);
  padding-right: var(--padding-container);
  margin: 0 auto;
  width: 100%;
  max-width: var(--max-width-container);
}

label {
  cursor: pointer;
}

.hover-button {
  transition: background-color 0.2s, border-color 0.2s, color 0.2s;
}

input:checked + .hover-button,
._pc .hover-button:hover {
  background-color: #6E48F5;
  border-color: #6E48F5;
  color: #ffffff;
}

input:checked + .hover-button .configuration__with-logo-image::after,
._pc .hover-button:hover .configuration__with-logo-image::after {
  opacity: 1;
}

.hover-button-icon {
  transition: background-color 0.2s, border-color 0.2s, color 0.2s;
}

.hover-button-icon svg path {
  transition: fill 0.2s;
}

._pc .hover-button-icon:hover {
  background-color: #6E48F5;
  color: #ffffff;
}

._pc .hover-button-icon:hover svg path {
  fill: #B7FE56;
}

.link {
  color: #B7FE56;
  text-decoration: underline;
  display: inline-flex;
  align-items: center;
  gap: 12px;
}

.link__icon {
  background-color: #B7FE56;
  border-radius: 3px;
  width: 36px;
  height: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.textarea,
.input {
  padding: 16px 20px;
  min-height: 65px;
  height: 100%;
  width: 100%;
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
  letter-spacing: -0.02em;
  border: 1px solid #B5B5B5;
  border-radius: 5px;
  background-color: #ffffff;
}

.textarea {
  height: 205px;
}

.checkbox {
  display: flex;
  gap: 10px;
}

.checkbox__icon {
  width: 20px;
  min-width: 20px;
  height: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #B5B5B5;
  border-radius: 3px;
  background-color: #ffffff;
}

.checkbox__icon svg {
  display: none;
}

input:checked + .checkbox__icon svg {
  display: block;
}

.checkbox__text {
  font-size: 18px;
  line-height: 140%;
  color: #ffffff;
}

.header-section {
  margin-bottom: 20px;
}

.header-with-button {
  display: flex;
  justify-content: space-between;
}

.title-section {
  font-family: var(--font-family-second);
  font-size: 30px;
  font-weight: 800;
  line-height: 100%;
  letter-spacing: -0.02em;
}

.text-section {
  margin-top: 20px;
  font-weight: 500;
  font-size: 20px;
  line-height: 140%;
}

.text-section span {
  color: #6E48F5;
}

.padding-section {
  margin-top: 10px;
  margin-bottom: 10px;
  padding-top: var(--indent-section);
  padding-bottom: var(--indent-section);
}

.margin-section {
  margin-top: var(--indent-section);
  margin-bottom: var(--indent-section);
}

.margin-bottom-section {
  margin-bottom: var(--indent-section);
}

.scroll::-webkit-scrollbar {
  width: 3px;
  height: 3px;
  border-radius: 6px;
}

.scroll::-webkit-scrollbar-track {
  border-radius: 6px;
  background-color: #F3F3F3;
}

.scroll::-webkit-scrollbar-thumb {
  padding: 2px;
  background-color: #575757;
  background-clip: content-box;
  border-radius: 6px;
}

.js-tabs-shell {
  will-change: height;
  transform: translateZ(0);
  height: 0;
  overflow: hidden;
}

.configuration {
  margin-left: -10px;
  padding: 10px;
  width: 100vw;
  background-color: #F3F3F3;
}

.configuration .button {
  width: 100%;
}

.configuration__body {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.configuration__side-main {
  flex: 1 1 auto;
}

.configuration__section:not(:last-child) {
  margin-bottom: 10px;
}

.configuration__section-title {
  padding: 10px 15px;
  font-weight: 600;
  font-size: 14px;
  color: #575757;
  line-height: 140%;
  border-radius: 3px;
  background-color: #ffffff;
}

.configuration__section-wrapper {
  padding: 15px 0;
}

.configuration__parameter:not(:last-child) {
  margin-bottom: 15px;
}

.configuration__parameter-name {
  margin-bottom: 10px;
  display: flex;
  align-items: center;
  gap: 5px;
  font-weight: 600;
  font-size: 14px;
  line-height: 140%;
}

.configuration__parameter-name a,
.configuration__parameter-name button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.16em;
}

.configuration__parameter-name svg {
  width: 10px;
  height: auto;
}

.configuration__group-buttons {
  display: flex;
  gap: 5px;
}

.configuration__group-buttons > * {
  flex: 1 1 1%;
}

.configuration__cpu {
  display: flex;
  gap: 10px;
  overflow-x: auto;
}

.configuration__cpu::-webkit-scrollbar {
  display: none;
}

.configuration__cpu:not(:last-child) {
  margin-bottom: 20px;
}

.configuration__cpu-icon {
  padding: 12px;
  display: block;
  background-color: #F3F3F3;
  border: 1px solid #B5B5B5;
  border-radius: 5px;
  width: 105px;
}

.configuration__cpu-icon img {
  width: 100%;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.04);
}

.configuration__with-logo {
  padding: 10px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  gap: 5px;
  width: 100%;
  min-height: 60px;
  border-radius: 5px;
  background-color: #F3F3F3;
  text-align: center;
  font-weight: 600;
  font-size: 13px;
  line-height: 140%;
  letter-spacing: -0.02em;
  border: 1px solid #B5B5B5;
}

.configuration__with-logo-image {
  position: relative;
}

.configuration__with-logo-image::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: #B7FE56;
  filter: blur(10px);
  opacity: 0;
  transition: opacity 0.3s;
}

.configuration__with-logo-image img {
  width: 38px;
  position: relative;
  z-index: 1;
}

.configuration__section-grid {
  display: grid;
  grid-template-columns: 1fr;
}

.configuration__selection-group {
  display: flex;
  align-items: stretch;
  gap: 5px;
  width: 100%;
}

.configuration__selection-group:not(:last-child) {
  padding-bottom: 10px;
}

.configuration__button-add {
  --width: 36px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--width);
  min-width: var(--width);
  min-height: 100%;
  border-radius: 5px;
  border: 1px solid #B5B5B5;
  overflow: hidden;
  background-color: #ffffff;
}

.configuration__button-add svg,
.configuration__button-add img {
  width: 37.5%;
}

.configuration__selection-message {
  padding: 2px 6px;
  display: inline-block;
  font-weight: 500;
  font-size: 12px;
  line-height: 140%;
  color: #ffffff;
  background-color: #FF383C;
  border-radius: 10px;
}

.configuration__filter-radio {
  margin-bottom: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

.configuration__filter-radio-list {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
}

.configuration__filter-radio-item {
  display: block;
  padding: 0.5em 1em;
  font-weight: 500;
  font-size: 13px;
  line-height: 140%;
  text-align: center;
  border: 1px solid #B5B5B5;
  background-color: #ffffff;
  border-radius: 100px;
}

.configuration__sort {
  position: relative;
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 13px;
  line-height: 140%;
}

.configuration__sort-list {
  position: absolute;
  top: calc(100% + 10px);
  left: 0;
  z-index: 10;
  width: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  gap: 10px;
  box-shadow: 0 4px 17px 0 rgba(0, 0, 0, 0.15);
  background-color: #ffffff;
  border-radius: 5px;
  font-size: 13px;
  line-height: 140%;
  opacity: 0;
  pointer-events: none;
  overflow: hidden;
}

.js-tabs-open .configuration__sort-list {
  opacity: 1;
  pointer-events: all;
}

.configuration__sort-list li {
  list-style: none;
}

._pc .configuration__sort-list button {
  transition: color 0.2s;
}

._pc .configuration__sort-list button:hover {
  color: #6E48F5;
}

.configuration__filter-return {
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  flex: 1 1 100px;
  background-color: #ffffff;
  border-radius: 3px;
  font-weight: 600;
  font-size: 22px;
  line-height: 140%;
}

.configuration__sort-value {
  display: flex;
  align-items: center;
  white-space: nowrap;
  color: #6E48F5;
  text-decoration: underline;
  text-decoration-style: dotted;
  transition: color 0.2s, text-decoration-color 0.2s;
}

.configuration__sort-value svg {
  width: 1em;
  height: auto;
  transition: transform 0.2s;
}

.configuration__sort-value svg path {
  transition: fill 0.2s;
}

.js-tabs-open .configuration__sort-value {
  color: #575757;
  text-decoration-color: transparent;
}

.js-tabs-open .configuration__sort-value svg {
  transform: rotate3d(1, 0, 0, 180deg);
}

.js-tabs-open .configuration__sort-value svg path {
  fill: #575757;
}

.configuration__filter-range {
  padding-top: 1em;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  gap: 10px;
  font-size: 22px;
}

.configuration__range {
  flex: 1 1 auto;
  display: flex;
  align-items: stretch;
  user-select: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.configuration__range-border {
  padding-bottom: 30px;
  padding-left: 30px;
  padding-right: 30px;
  border: 1px solid #B5B5B5;
  border-radius: 3px;
  width: 100%;
}

.configuration__range-title {
  padding-left: 10px;
  padding-right: 10px;
  display: inline-block;
  font-weight: 600;
  line-height: 140%;
  transform: translateY(-50%);
  background-color: #f3f3f3;
}

.configuration__range-group {
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.configuration__range-block {
  flex: 1 1 auto;
  gap: 20px;
}

.configuration__range-name {
  font-size: 20px;
  line-height: 140%;
}

.configuration__double-range {
  flex: 1 0 300px;
  --size-spin: 19px;
  font-size: 18px;
  padding-top: calc(var(--size-spin) + 1em);
  padding-bottom: calc(var(--size-spin) / 2);
}

.configuration__double-range-track {
  position: relative;
  height: 6px;
  border-radius: 100px;
  background: linear-gradient(90deg, #6E48F5 0% var(--minGradient), #ffffff var(--minGradient) var(--maxGradient), #6E48F5 var(--maxGradient) 100%);
}

.configuration__double-range-spin {
  cursor: pointer;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: var(--size-spin);
  min-width: var(--size-spin);
  height: var(--size-spin);
  background-color: #191919;
  border: 3px solid #191919;
  border-radius: 50%;
  transition: background-color 0.2s, border-color 0.2s;
}

.configuration__double-range-spin.hover,
.configuration__double-range-spin:hover {
  background-color: #ffffff;
  border: 3px solid #575757;
}

.configuration__double-range-input {
  position: absolute;
  display: block;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  text-align: center;
  font-size: inherit;
  width: 5em;
  line-height: 140%;
  color: #575757;
}

.configuration__filter-checkboxes {
  margin-top: 18px;
  display: flex;
  flex-wrap: wrap;
  gap: 15px;
}

.configuration__filter-checkbox {
  display: flex;
  align-items: center;
  gap: 10px;
  font-size: 20px;
  line-height: 140%;
}

.configuration__filter-checkbox-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 20px;
  min-width: 20px;
  height: 20px;
  border: 1px solid #B5B5B5;
  border-radius: 3px;
  background-color: #ffffff;
  transition: background-color 0.2s;
}

.configuration__filter-checkbox-icon svg,
.configuration__filter-checkbox-icon img {
  display: none;
  width: 55%;
  height: auto;
}

input:checked + .configuration__filter-checkbox-icon {
  background-color: #6E48F5;
}

input:checked + .configuration__filter-checkbox-icon svg,
input:checked + .configuration__filter-checkbox-icon img {
  display: block;
}

.configuration__ready-grid {
  margin-top: 30px;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 1fr;
  gap: 10px;
}

.configuration__parameter-list {
  --padding-parameter: 5px;
  display: grid;
  grid-template-columns: 1fr 1.35fr;
  font-size: 12px;
}

.configuration__parameter-list._center {
  align-items: center;
}

.configuration__parameter-list-name {
  padding-top: var(--padding-parameter);
  padding-bottom: var(--padding-parameter);
  padding-right: 20px;
  line-height: 140%;
  color: #B5B5B5;
  border-bottom: 1px solid #F3F3F3;
}

.configuration__parameter-list-value {
  padding-top: var(--padding-parameter);
  padding-bottom: var(--padding-parameter);
  font-weight: 500;
  line-height: 140%;
  border-bottom: 1px solid #F3F3F3;
}

.configuration__parameter-list-name-strong {
  padding-top: var(--padding-parameter);
  padding-bottom: var(--padding-parameter);
  font-weight: 600;
  font-size: 13px;
  line-height: 140%;
  border-bottom: 1px solid #F3F3F3;
}

.configuration__parameter-list-name-strong:nth-last-child(2) {
  border: none;
}

.configuration__parameter-list-name-strong:nth-last-child(2) + .configuration__parameter-list-value {
  border: none;
}

.configuration__parameter-list-result {
  display: flex;
  align-items: flex-end;
  font-weight: 600;
  font-size: 15px;
  line-height: 140%;
}

.configuration__parameter-list-result-number {
  font-family: var(--font-family-second);
  font-weight: 800;
  font-size: 26px;
  line-height: 100%;
  letter-spacing: -0.02em;
  text-align: right;
}

.configuration__parameter-list-result-number span {
  font-size: 0.6em;
}

.configuration__parameter-info {
  margin-top: 15px;
  font-size: 11px;
  line-height: 140%;
  text-align: center;
  color: #575757;
}

.configuration__note {
  margin-top: 20px;
  font-size: 13px;
  line-height: 140%;
  color: #575757;
}

.configuration__actions {
  padding: 10px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 10px;
}

.configuration__actions a,
.configuration__actions button {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 13px;
  line-height: 140%;
  letter-spacing: -0.02em;
  background-color: #ffffff;
  border: 1px solid #B5B5B5;
  border-radius: 8px;
}

.tabs-switching-buttons {
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.tabs-switching-buttons a,
.tabs-switching-buttons button {
  padding: 10px 20px;
  flex: 1 1 1%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 55px;
  border: 1px solid #B5B5B5;
  border-radius: 10px;
  background-color: transparent;
  transition: background-color 0.3s, border-color 0.3s;
}

._pc .tabs-switching-buttons a:hover,
.tabs-switching-buttons a.active,
._pc .tabs-switching-buttons button:hover,
.tabs-switching-buttons button.active {
  border-color: #6E48F5;
  background-color: #6E48F5;
}

._pc .tabs-switching-buttons a:hover .tabs-switching-buttons__text,
.tabs-switching-buttons a.active .tabs-switching-buttons__text,
._pc .tabs-switching-buttons button:hover .tabs-switching-buttons__text,
.tabs-switching-buttons button.active .tabs-switching-buttons__text {
  color: #ffffff;
}

._pc .tabs-switching-buttons a:hover .tabs-switching-buttons__info,
.tabs-switching-buttons a.active .tabs-switching-buttons__info,
._pc .tabs-switching-buttons button:hover .tabs-switching-buttons__info,
.tabs-switching-buttons button.active .tabs-switching-buttons__info {
  color: #B7FE56;
}

.tabs-switching-buttons__text {
  font-family: var(--font-family-second);
  font-weight: 800;
  font-size: 16px;
  line-height: 110%;
  letter-spacing: -0.01em;
  text-align: center;
  transition: color 0.3s;
}

.tabs-switching-buttons__info {
  color: #6E48F5;
  font-size: 13px;
  transition: color 0.3s;
}

.button {
  padding: 7px 15px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  min-height: 35px;
  width: 100%;
  font-family: var(--font-family-second);
  font-weight: 800;
  font-size: 14px;
  line-height: 140%;
  letter-spacing: -0.02em;
  background-color: var(--background);
  border-radius: 100px;
